* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.content {
  min-height: 100vh;
}

h1 {
  text-align: center;
  padding: 2rem 0rem;
}

p {
  text-align: center;
  padding: 2rem 0rem;
}

a {
  text-decoration: none;
  color: black;
}

ul {
  list-style-type: none;
}

.morePicture {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 0rem;
  button {
    font-size: 1rem;
    padding: 0.25rem 1rem;
    border-radius: 7.5%;
    border: none;
    background-color: rgb(141, 13, 141);
    color: white;
    cursor: pointer;
  }
}

@import "./nav";
@import "./search";
@import "./footer";
@import "./picture";
